  height: 100%;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p:hover{
  text-decoration: none;

}




.nav-item a{
    color:  #fb2968;
    font-weight: bold;
    position: relative;
    top: 0;
    transition-property: all;
